import React, {Fragment}from "react";
import { Layout, Menu, Breadcrumb } from "antd";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import * as actions from "../store/actions/auth";
import { fetchCart } from "../store/actions/cart";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCartPlus } from '@fortawesome/free-solid-svg-icons'


class CustomLayout extends React.Component {

  componentDidMount() {
    if (this.props.token !== undefined && this.props.token !== null) {
      this.props.refreshCart(this.props.token);
    }
  }

  componentWillReceiveProps(newProps) {
    if (newProps.token !== this.props.token) {
      if (newProps.token !== undefined && newProps.token !== null) {

        this.props.refreshCart(newProps.token);

      }
    }
  }


  render() {
    const { cart } = this.props;

    return (
      <>
      <div className=" ">
          <ul className="navblock">
             <div className="container mx-auto">
             <li className="navlist active">
                <Link to="/">Home</Link>
              </li>
              <li className="navlist">
                <Link to="/products">Shop</Link>
            </li>
              {this.props.isAuthenticated ? (
                <Fragment>
              {
                this.props.seller ?(
                  <li className="navlist">
                      <Link to='/farmer-dashboard'>Dashboard</Link>
                  </li>
                ):(
                  <li> </li>
                )
              }
              <li className="navlist">
                  <Link to='/cart'>
                  <FontAwesomeIcon icon={faCartPlus} size="1x" />
                  {`${cart !== null ? cart.order_items.length : 0}`}</Link>
              </li>
              <li className="navlist">
                  <a href='/'onClick={this.props.logout} >Logout</a>
              </li>
                </Fragment>
      ) : (
         <Fragment>
              <li className="navlist">
              <Link to="/login">Login</Link>
              </li>
              <li className="navlist">
              <Link to="/register">Sign up</Link>
              </li>
         </Fragment>

      )}

             </div>
      </ul>
  </div>

  <div className="container mx-auto items-stretch ">
          {this.props.children}
  </div>
{/* <Footer /> */}

      </>
    );
  }
}

const mapStateToProps = state => {
  return {
    userId: state.auth.userId,
    token: state.auth.token,
    seller: state.auth.is_seller,
    authenticated: state.auth.token !== null,
    cart: state.cart.shoppingCart,
    loading: state.cart.loading

  };
};

const mapDispatchToProps = dispatch => {
  return {
    logout: () => dispatch(actions.logout()),
    refreshCart: (token) => dispatch(fetchCart(token))

  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(CustomLayout)
);
